import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Card, Row, Col, Input, message, Button } from 'antd';
// import { commentEndCourse } from 'dump/data';
import { TableData } from 'components/common';
import {
  getEstimatedNote,
  updatEstimatedNote,
  downloadFileEstimatedNote,
} from 'services/EstimatedNote';
import FormSerchCommentEndCourse from './FormSerchCommentEndCourse';

class ListCommentEndCoursePage extends Component {
  state = {
    reload: 0,
    listId: [],
    estimatedNote: '',
  };

  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
      reload: this.state.reload + 1,
    });
  };

  // exportAddReward = () => {
  //   const { paramSearchClass } = this.state;
  //   exportDataAddReward(paramSearchClass);
  // };

  saveDataCommentEndCourse = () => {
    const { listId, estimatedNote } = this.state;
    const dataUpdate = {
      ids: listId,
      estimatedNote,
    };
    updatEstimatedNote(dataUpdate).then(() => {
      message.success('Chnh sa thnh cng');
    });
  };

  changeEstimatedNote = e => {
    this.setState({
      estimatedNote: e.target.value,
    });
  };

  dowloadFilePdf = () => {
    const { paramSearchClass } = this.state;
    downloadFileEstimatedNote(paramSearchClass);
  };

  render() {
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'toScores',
      },
      {
        title: 'Nhn xt',
        dataIndex: 'pobText',
        render: (data, record) => (
          <Fragment>
            <Input.TextArea autosize defaultValue={record.estimatedNote} />
          </Fragment>
        ),
      },
    ];
    const { reload, paramSearchClass } = this.state;
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const listId = [];
        selectedRows.map(record => listId.push(record.studentId));
        this.setState({
          listId,
        });
      },
    };
    return (
      <Card title="Nhn xt cui kha">
        <Row>
          <Col span={24}>
            <FormSerchCommentEndCourse
              dataForm={this.dataFormSeach}
              changeNote={this.changeEstimatedNote}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              rowSelection={rowSelection}
              reload={reload}
              formSearch
              service={getEstimatedNote}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
          <Col span={24} />
          <Col offset={8} span={12}>
            <BtnSave onClick={this.saveDataCommentEndCourse} htmlType="submit" />
            <Button
              style={{ height: 35, marginLeft: 8, marginRight: 8 }}
              type="primary"
              onClick={this.dowloadFilePdf}
              icon="download"
              size="large"
            >
              Download
            </Button>
            <BtnCancel onClick={this.cancelUpload} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCommentEndCoursePage />
  </Page>
);



